import * as React from 'react';
import { mount, ReactWrapper } from 'enzyme';
import SplunkThemeProvider from '@splunk/themes/SplunkThemeProvider';

/* eslint-disable react/prop-types, react/display-name */
export const mountWithTheme = (Component, themeToTest = {}): ReactWrapper =>
    mount(Component, {
        wrappingComponent: ({ children }): React.ReactElement => (
            <SplunkThemeProvider
                additionalThemeProperties={{ ...themeToTest }}
                colorScheme="light"
                density="comfortable"
                family="enterprise"
            >
                {children}
            </SplunkThemeProvider>
        ),
    });
/* eslint-enable react/prop-types, react/display-name */
